import React, { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { Row, Col, Typography, Button, Card, Statistic, Table, Form, Spin, Tooltip } from 'antd'
import * as anticons from '@ant-design/icons'

import { getLabel, labels } from '../../utils/Internationalization'

import UsuarioService from '../../service/UsuarioService'
import toast from 'react-hot-toast'
import AlertWait from '../../pages/AlertWait'

const usuarioService = new UsuarioService()

const tableProps = {
    bordered: false,
    loading: false,
    title: '',
    showHeader: true,
    size: 'medium',
    pagination: {
        position: ['none', 'bottomRight']
    }
}

const estilos = [
    { background: '#54873C' },
    { background: '#70B450' },
    { background: '#385A28' },
    { background: '#636466' },
    { background: '#70B450' },
    { background: '#54873C' },
    { background: '#385A28' },
]

const { Paragraph, Text } = Typography

const Dashboard = () => {

    const navigate = useHistory()
    const [load, setLoad] = useState(true)
    const [loadTotals, setLoadTotals] = useState(true)
    const [isLoading, setIsLoading] = useState(false)
    const [users, setUsers] = useState([])
    const [isLoadingTotals, setIsLoadingTotals] = useState(false)
    const [totals, setTotals] = useState(null)
    const [total, setTotal] = useState(0)
    const [nombres, setNombres] = useState([])
    const [apellidos, setApellidos] = useState([])
    const [paises, setPaises] = useState([])
    const [perfiles, setPerfiles] = useState([])

    const columnas = [
        {
            title: '',
            render: (_, r) => (r.imagen && <img alt='' src={`/images/${r.imagen.split('/')[r.imagen.split('/').length - 1]}`} width={32} height={32} />) || <img alt='' src='./assets/user.png' width={32} height={32} />
        },
        {
            title: getLabel(labels.firstName),
            dataIndex: 'nombres',
            filters: nombres.map(n => ({
                value: n,
                text: n
            })),
            filterMode: 'tree',
            onFilter: (value, { nombres }) => nombres === value,
            render: (_, r) => `${r.nombres}`
        },
        {
            title: getLabel(labels.lastName),
            dataIndex: 'apellidos',
            filters: apellidos.map(a => ({
                value: a,
                text: a
            })),
            filterMode: 'tree',
            onFilter: (value, { apellidos }) => apellidos === value,
            render: (_, r) => `${r.apellidos}`
        },
        {
            title: getLabel(labels.country),
            dataIndex: 'pais',
            filters: paises.map(p => ({
                value: p.catId,
                text: <><img
                    src={`https://flagcdn.com/32x24/${p.catDescripcion.toLowerCase()}.png`}
                    srcSet={`https://flagcdn.com/32x24/${p.catDescripcion.toLowerCase()}.png 2x,https://flagcdn.com/96x72/${p.catDescripcion.toLowerCase()}.png 3x`}
                    width="32"
                    height="24"
                    alt={p.catDescripcion} />&nbsp;{p.catNombre}</>
            })),
            filterMode: 'tree',
            onFilter: (value, { pais }) => pais.catId === value,
            render: (_, { pais }) => <div style={{ textAlign: 'center' }}>
                <Tooltip title={pais.catDescripcion}>
                    <img
                        src={`https://flagcdn.com/32x24/${pais.catDescripcion.toLowerCase()}.png`}
                        srcSet={`https://flagcdn.com/32x24/${pais.catDescripcion.toLowerCase()}.png 2x,https://flagcdn.com/96x72/${pais.catDescripcion.toLowerCase()}.png 3x`}
                        width="32"
                        height="24"
                        alt={pais.catDescripcion}></img>
                </Tooltip>
            </div>,
            responsive: ['md', 'lg', 'xl', 'xxl']
        },
        {
            title: <div style={{ textAlign: 'center' }}>
                {getLabel(labels.profile)}
            </div>,
            dataIndex: 'role',
            filters: perfiles.map(p => ({
                value: p.perId,
                text: p.perNombre
            })),
            filterMode: 'tree',
            onFilter: (value, { perfiles }) => perfiles[0].perId === value,
            render: (_, { perfiles }) => <div style={{ textAlign: 'center' }
            }>
                {perfiles[0].perNombre}
            </div >,
            responsive: ['md', 'lg', 'xl', 'xxl']
        },
        {
            title: getLabel(labels.actions),
            render: (_, record) => <Button type='primary' href={`/#/user/${record.usuId}`}>
                {getLabel(labels.view)}
            </Button>
        }
    ]

    const renderTotal = ({ id, name, props, data, description }) =>
        <Col
            key={id}
            xs={12} sm={8} md={8} lg={8} xl={8} xxl={8}
        >
            <Card
                {...props}
                onClick={() => fetchDataByProfile({ perId: id, perNombre: name })}
            >
                <Statistic
                    {...data}
                    valueStyle={{
                        color: '#ffffff',
                    }}
                />
                {description}
            </Card>
        </Col >

    const fetchDataTotals = async () => {
        setLoadTotals(false)
        setIsLoadingTotals(true)
        try {
            const { data } = await usuarioService.getTotalUsersByProfile({ navigate })
            if (data) {
                const totalesCompleto = data.listObject
                const totales = totalesCompleto.filter(t => t.perNombre.indexOf('ADMIN') < 0 && t.perNombre.indexOf('BENEFI') < 0)
                const initialValue = 0;
                const sumWithInitial = totales.reduce(
                    (accumulator, currentValue) => accumulator + currentValue.total,
                    initialValue,
                )
                setTotal(sumWithInitial)
                setTotals([...totales, totalesCompleto.filter(t => t.perNombre.indexOf('BENEFI') >= 0)[0]])
            } else {
                toast.error(getLabel(labels.errorOcurred))
            }
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : getLabel(labels.errorOcurred))
        } finally {
            setIsLoadingTotals(false)
        }
    }

    const fetchDataByProfile = async (profile) => {
        setIsLoading(true)
        setUsers([])
        setPerfiles([])
        try {
            const { data } = await usuarioService.getUsersListByProfile({ navigate, profile })
            if (data) {
                const perfiles = [profile]
                const usuarios = data.listObject.map(u => ({ ...u, perfiles }))
                let n = []
                usuarios.forEach(u => {
                    if (n.filter(p => p === u.nombres).length < 1)
                        n = [...n, u.nombres]
                })
                let a = []
                usuarios.forEach(u => {
                    if (a.filter(p => p === u.apellidos).length < 1)
                        a = [...a, u.apellidos]
                })
                let p = []
                usuarios.forEach(u => {
                    if (p.filter(p => p.perId === u.perfiles[0].perId).length < 1)
                        p = [...p, u.perfiles[0]]
                })
                let c = []
                usuarios.forEach(u => {
                    if (c.filter(p => p.catId === u.pais.catId).length < 1)
                        c = [...c, u.pais]
                })
                setNombres(n)
                setApellidos(a)
                setPaises(c)
                setPerfiles(p)
                setUsers(usuarios)
            } else {
                toast.error(getLabel(labels.errorOcurred))
            }
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : getLabel(labels.errorOcurred))
        } finally {
            setIsLoading(false)
        }
    }

    const fetchData = async () => {
        setLoad(false)
        setIsLoading(true)
        setUsers([])
        setPerfiles([])
        try {
            const { data } = await usuarioService.getLessPostulants({ navigate })
            if (data) {
                const usuarios = data.listObject
                let n = []
                usuarios.forEach(u => {
                    if (n.filter(p => p.nombres === u.nombres).length < 1)
                        n = [...n, u.nombres]
                })
                let a = []
                usuarios.forEach(u => {
                    if (a.filter(p => p.apellidos === u.apellidos).length < 1)
                        a = [...a, u.apellidos]
                })
                let p = []
                usuarios.forEach(u => {
                    if (p.filter(p => p.perId === u.perfiles[0].perId).length < 1)
                        p = [...p, u.perfiles[0]]
                })
                let c = []
                usuarios.forEach(u => {
                    if (c.filter(p => p.catId === u.pais.catId).length < 1)
                        c = [...c, u.pais]
                })
                setNombres(n)
                setApellidos(a)
                setPaises(c)
                setPerfiles(p)
                setUsers(usuarios)
            } else {
                toast.error(getLabel(labels.errorOcurred))
            }
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : getLabel(labels.errorOcurred))
        } finally {
            setIsLoading(false)
        }
    }

    useEffect(() => {
        if (load) fetchData()
        if (loadTotals) fetchDataTotals()
    })

    return (
        <>
            {
                isLoadingTotals ?
                    <AlertWait isLoading={isLoadingTotals} message={getLabel(labels.loading)} />
                    :
                    totals && <Row gutter={[15, 15]}>
                        {
                            totals && totals.map((t, index) => (
                                {
                                    id: t.perId,
                                    name: t.perNombre,
                                    props: {
                                        bordered: true,
                                        hoverable: true,
                                        style: {
                                            height: '100%'
                                            , color: '#f0f0f0'
                                            , ...estilos[index]
                                        }
                                    },
                                    data: {
                                        title: <Typography.Text style={{ color: '#ffffff' }}>{t.perNombre}</Typography.Text>,
                                        value: t.perId !== 0 ? Math.round(t.total * 100.00 / total, 1) : t.total,
                                        precision: 0,
                                        prefix: <anticons.UserOutlined />,
                                        suffix: t.perId !== 0 && '%'
                                    },
                                    description: t.perId !== 0 && <>{t.total} {getLabel(labels.of)} {total} {getLabel(labels.users)}</>
                                }
                            )).map(s => renderTotal(s))
                        }
                    </Row>
            }
            <Row justify='end'>
                <Col style={{ padding: '15px 0' }}>
                    <Form
                        layout='inline'
                    >
                        <Form.Item>
                            <Button type='default'
                                onClick={() => fetchData()}
                            >
                                {getLabel(labels.refresh)}
                            </Button>
                        </Form.Item>
                    </Form>
                </Col>
            </Row>
            {
                isLoading ?
                    <Row style={{ padding: '30px 10px' }}>
                        <Col span={24} style={{ textAlign: 'center' }}>
                            <Spin size='large' tip={
                                <Typography.Paragraph style={{ padding: '10px 0' }}>
                                    {getLabel(labels.loading)}...
                                </Typography.Paragraph>
                            } />
                        </Col>
                    </Row>
                    :
                    <Row>
                        <Col span={24}>
                            <Typography>
                                <Paragraph>
                                    <Text strong>
                                        {
                                            perfiles.length === 1 && perfiles.map(p => (<div key={p.perId}>{p.perNombre}</div>))
                                        }
                                    </Text>
                                    <Text type='secondary'>
                                        {`${users.length} ${getLabel(labels.resultsFound)}`}
                                    </Text>
                                </Paragraph>
                            </Typography>
                        </Col>
                        <Col span={24}>
                            {
                                perfiles && <Table
                                    {...tableProps}
                                    rowKey='usuId'
                                    columns={columnas}
                                    dataSource={users}>
                                </Table>
                            }
                        </Col>
                    </Row >
            }
        </>
    )
}

export default Dashboard